/* src/styles/AdminUserInquiries.css */

.admin-user-inquiries-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.sidebar {
  width: 250px;
  background-color: #2c3e50;
  color: #ecf0f1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.sidebar-logo {
  width: 150px;
  height: auto;
  margin-bottom: 30px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  width: 100%;
  flex-grow: 1;
}

.sidebar-menu li {
  padding: 15px 20px;
  cursor: pointer;
  width: 100%;
  text-align: center;
  transition: background 0.3s;
  box-sizing: border-box;
}

.sidebar-menu li:hover,
.sidebar-menu li.active {
  background-color: #34495e;
}

.logout-button {
  margin-top: auto;
  padding: 10px 20px;
  background-color: #e74c3c;
  border: none;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s;
  border-radius: 3px;
  width: 100%;
  box-sizing: border-box;
}

.logout-button:hover {
  background-color: #c0392b;
}

.content {
  flex: 1;
  padding: 40px;
  background-color: #ecf0f1;
  overflow-y: auto;
  box-sizing: border-box;
}

.content-title {
  font-size: 1.8rem;
  margin-bottom: 20px;
  text-align: left;
}

.inquiry-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.inquiry {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.inquiry-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.inquiry-title {
  font-weight: bold;
}

.inquiry-status {
  padding: 5px 10px;
  border-radius: 3px;
}

.inquiry-status.complete {
  background-color: #2ecc71;
  color: #fff;
}

.inquiry-answer-button {
  padding: 5px 10px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background 0.3s;
}

.inquiry-answer-button:hover {
  background-color: #2980b9;
}

.inquiry-content {
  margin-bottom: 10px;
}

.inquiry-answer {
  background-color: #f1f1f1;
  padding: 10px;
  border-radius: 3px;
}

.answer-form {
  margin-top: 10px;
  display: flex;
  flex-direction: column;
}

.answer-form textarea {
  resize: vertical;
  min-height: 60px;
  padding: 10px;
  border: 1px solid #bdc3c7;
  border-radius: 3px;
  margin-bottom: 10px;
}

.answer-buttons {
  display: flex;
  gap: 10px;
}

.submit-button {
  padding: 8px 16px;
  background-color: #27ae60;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background 0.3s;
}

.submit-button:hover {
  background-color: #1e8449;
}

.cancel-button {
  padding: 8px 16px;
  background-color: #95a5a6;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background 0.3s;
}

.cancel-button:hover {
  background-color: #7f8c8d;
}

.error-message {
  color: red;
  font-weight: bold;
  margin-bottom: 10px;
}

.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.pagination button {
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  background-color: #3498db;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s;
}

.pagination button:hover:not([disabled]) {
  background-color: #2980b9;
}

.pagination button:disabled {
  background-color: #bdc3c7;
  cursor: not-allowed;
}
