.profile-edit-container {
  width: 100%;
  max-width: 400px;
  margin: 50px auto;
  padding: 30px;
  border-radius: 12px;
  background-color: #ffffff;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  text-align: center;
}

h2 {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
}

.info-fields {
  text-align: left;
  margin-bottom: 20px;
  color: #555;
}

.field {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
}

.field label {
  font-weight: bold;
  color: #333;
  margin-bottom: 5px;
  font-size: 14px;
}

.field input {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
}

.field span {
  color: #555;
  font-weight: bold;
  line-height: 2;
}

.error-message {
  color: #d9534f;
  background-color: #f2dede;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.success-message {
  color: #5cb85c;
  background-color: #dff0d8;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}

.edit-button {
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #333;
  color: white;
  transition: background-color 0.3s;
}

.edit-button:hover {
  background-color: #555;
}

.delete-button {
  padding: 12px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #d9534f;
  color: white;
  transition: background-color 0.3s;
  margin-top: 20px;
}

.delete-button:hover {
  background-color: #c9302c;
}

@media (max-width: 768px) {
  .profile-edit-container {
    padding: 20px;
  }

  .field input {
    font-size: 13px;
  }

  .edit-button, .delete-button {
    font-size: 14px;
  }
}
