/* MyPage.css */
.mypage-container {
  width: 100%;
  max-width: 500px;
  margin: 40px auto;
  text-align: center;
}

.mypage-title {
  font-size: 24px;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
}

.profile-section {
  margin-bottom: 40px;
}

.profile-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #333;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 48px;
  font-weight: bold;
  margin: 0 auto;
}

.button-group {
  display: grid;
  grid-template-columns: 1fr 1fr; /* 두 열로 나누어 배치 */
  gap: 20px; /* 버튼 간 간격을 20px로 설정 */
  justify-items: center; /* 버튼을 가운데 정렬 */
  max-width: 400px;
  margin: 0 auto;
}

.action-button {
  width: 100%; /* 버튼 너비를 100%로 설정하여 균일하게 */
  padding: 15px 0;
  font-size: 16px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.action-button:hover {
  background-color: #555;
}
