.admin-review-board-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.sidebar {
  width: 250px;
  background-color: #2c3e50;
  color: #ecf0f1;
}

.content {
  flex: 1;
  padding: 40px;
  background-color: #ecf0f1;
  overflow-y: auto;
}

.content-title {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.review-list {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.review-header,
.review-row {
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.review-actions button {
  padding: 5px 10px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  transition: background 0.3s;
}

.review-actions button:hover {
  background-color: #2980b9;
}
