/* src/styles/AdminMusicHistory.css */

.admin-community-board-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.admin-sidebar {
  width: 250px;
  background-color: #000; /* 짙은 블랙 배경 */
  color: #fff; /* 텍스트 흰색 */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.sidebar-logo {
  width: 150px;
  height: auto;
  margin-bottom: 30px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  width: 100%;
}

.sidebar-menu li {
  padding: 15px 20px;
  cursor: pointer;
  text-align: center;
  transition: background 0.3s ease;
  color: #ccc; /* 연한 회색 텍스트 */
}

.sidebar-menu li:hover,
.sidebar-menu li.active {
  background-color: #333;
  color: #fff; /* 활성 상태에서 흰색 텍스트 */
}

.logout-button {
  margin-top: auto;
  padding: 10px 20px;
  background-color: #555;
  border: none;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box;
  transition: background 0.3s ease;
}

.logout-button:hover {
  background-color: #444; /* 더 짙은 회색 */
}

.main-content {
  flex: 1;
  padding: 40px;
  background-color: #ecf0f1; /* 밝은 회색 */
  overflow-y: auto;
  box-sizing: border-box;
}

.content-title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #2c3e50; /* 다크 블루 텍스트 */
}

/* 테이블 스타일링 */
.music-history-table {
  width: 100%;
  border-collapse: collapse;
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.music-history-table th,
.music-history-table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: center;
}

.music-history-table th {
  background-color: #3498db;
  color: #fff;
  font-weight: bold;
}

.music-history-table tr:nth-child(even) {
  background-color: #f2f2f2;
}

.music-history-table tr:hover {
  background-color: #f1f1f1;
}

/* 삭제 버튼 스타일링 */
.delete-button {
  padding: 8px 12px;
  background-color: #e74c3c;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.delete-button:hover {
  background-color: #c0392b;
  transform: translateY(-2px);
}

/* 삭제 셀 내 버튼 중앙 정렬 */
.delete-cell {
  display: center;
  justify-content: center;
  align-items: center;
}

/* 페이징 컨트롤 스타일링 */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 10px 16px;
  border: none;
  border-radius: 5px;
  background-color: #3498db;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
  font-size: 1rem;
}

.pagination button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination span {
  font-size: 1rem;
  margin: 0 10px;
}

.no-data-message {
  text-align: center;
  font-style: italic;
  color: #555;
}

.error-message {
  color: red;
  margin-bottom: 10px;
}

/* 반응형 디자인 */
@media (max-width: 600px) {
  .admin-community-board-container {
    flex-direction: column;
  }

  .admin-sidebar {
    width: 100%;
    flex-direction: row;
    justify-content: space-around;
  }

  .sidebar-menu li {
    padding: 10px;
    font-size: 0.9rem;
  }

  .main-content {
    padding: 20px;
  }

  .music-history-table th,
  .music-history-table td {
    padding: 8px;
    font-size: 0.9rem;
  }

  .pagination button {
    padding: 8px 12px;
    font-size: 0.9rem;
  }

  .pagination span {
    font-size: 0.9rem;
  }
}
