/* App.css */
body {
  font-family: 'Arial', sans-serif;
  background-color: #ffffff;
  margin: 0;
  padding: 0;
}

.app-container {
  padding-top: 80px; /* 헤더 높이만큼 상단 여백 추가 */
}


.header {
  padding: 20px;
  background-color: #f8f9fa;
  position: fixed;
  top: 0;
  width: 100%;
}

.content {
  padding-top: 80px;
  max-width: 1200px;
  margin: 0 auto;
}

button {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 8px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #555;
}
