.admin-user-management-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.admin-sidebar {
  width: 250px;
  background-color: #000; /* 짙은 블랙 배경 */
  color: #fff; /* 흰색 텍스트 */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.sidebar-logo {
  width: 150px;
  height: auto;
  margin-bottom: 30px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  width: 100%;
  flex-grow: 1;
}

.sidebar-menu li {
  padding: 15px 20px;
  cursor: pointer;
  width: 100%;
  text-align: center;
  transition: background 0.3s;
  box-sizing: border-box;
  color: #ccc;
}

.sidebar-menu li:hover,
.sidebar-menu li.active {
  background-color: #333;
  color: #fff;
}

.logout-button {
  margin-top: auto;
  padding: 10px 20px;
  background-color: #555;
  border: none;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s;
  border-radius: 3px;
  width: 100%;
  box-sizing: border-box;
}

.logout-button:hover {
  background-color: #444;
}

.main-content {
  flex: 1;
  padding: 40px;
  background-color: #ecf0f1; /* 밝은 회색 배경 */
  overflow-y: auto;
  box-sizing: border-box;
}

.content-title {
  font-size: 1.8rem;
  margin-bottom: 20px;
  text-align: left;
  color: #000;
}

.user-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.user-table th,
.user-table td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

.user-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

.user-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.user-table tr:hover {
  background-color: #eaeaea;
}

.delete-button {
  padding: 8px 12px;
  background-color: #e74c3c;
  border: none;
  color: #fff;
  cursor: pointer;
  border-radius: 3px;
  transition: background 0.3s;
}

.delete-button:hover {
  background-color: #c0392b;
}

.error-message {
  color: red;
  font-weight: bold;
  margin-bottom: 10px;
}

.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

.pagination button {
  padding: 8px 16px;
  border: none;
  background-color: #3498db;
  color: #fff;
  cursor: pointer;
  border-radius: 3px;
  transition: background 0.3s;
}

.pagination button:hover:not([disabled]) {
  background-color: #2980b9;
}

.pagination button:disabled {
  background-color: #95a5a6;
  cursor: not-allowed;
}

.pagination span {
  margin: 0 10px;
  color: #333;
}
