/* 전체 컨테이너 */
.history-container {
  width: 80%;
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* 제목 스타일 */
.history-container h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

/* 음악 기록 리스트 스타일 */
.history-list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.history-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px;
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 8px;
  margin-bottom: 10px;
  transition: box-shadow 0.3s;
}

.history-item:hover {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

/* 상세보기 버튼 스타일 */
.detail-button {
  padding: 8px 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s;
}

.detail-button:hover {
  background-color: #0056b3;
}

/* 페이지네이션 */
.pagination {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  gap: 10px;
}

.pagination button {
  padding: 8px 16px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
}

.pagination button:hover {
  background-color: #555;
}

.pagination span {
  display: inline-block;
  padding: 8px 16px;
  font-size: 14px;
}
