.review-board-container {
  width: 70%;
  margin: 20px auto;
  padding: 20px;
  background-color: #f8f9fa;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  margin-bottom: 20px;
  color: #343a40;
}

.review-list {
  list-style: none;
  padding: 0;
}

.review-item {
  background: #ffffff;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.review-item-link {
  text-decoration: none;
  color: inherit; /* 링크의 기본 색상 제거 */
}

.review-item h3 {
  font-size: 18px;
  font-weight: bold;
  margin: 0 0 10px;
  color: #2c3e50;
}

.review-item p {
  margin: 5px 0;
  color: #7f8c8d;
}

.review-item strong {
  color: #34495e;
}

.admin-reply {
  background-color: #ecf0f1;
  padding: 10px;
  border-radius: 5px;
  margin-top: 10px;
}

.admin-reply h4 {
  margin: 0 0 5px;
  font-size: 14px;
  color: #c0392b;
}

.admin-reply p {
  margin: 0;
  font-size: 13px;
  color: #7f8c8d;
}

hr {
  border: 0;
  border-top: 1px solid #ecf0f1;
  margin: 10px 0;
}

.pagination {
  text-align: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  background-color: #e0e0e0;
  color: #333;
  cursor: pointer;
}

.pagination button.active {
  background-color: #2ecc71;
  color: white;
  font-weight: bold;
}

.pagination button:disabled {
  background-color: #bdc3c7;
  cursor: default;
}

.write-post {
  text-align: center;
  margin-top: 30px;
}

.write-button {
  background-color: #2ecc71;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 3px;
  cursor: pointer;
  font-size: 16px;
}

.write-button:hover {
  background-color: #27ae60;
}
