.admin-sidebar {
  width: 250px;
  background-color: #000; /* 짙은 블랙 배경 */
  color: #fff; /* 텍스트는 흰색으로 */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
  height: 100vh;
}

.sidebar-logo {
  width: 150px;
  height: auto;
  margin-bottom: 30px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  width: 100%;
  flex-grow: 1;
}

.sidebar-menu li {
  padding: 15px 20px;
  cursor: pointer;
  width: 100%;
  text-align: center;
  transition: background 0.3s;
  box-sizing: border-box;
  color: #ccc; /* 텍스트는 연한 회색 */
}

.sidebar-menu li:hover,
.sidebar-menu li.active {
  background-color: #333; /* 호버 및 활성 상태: 더 밝은 블랙 */
  color: #fff; /* 호버 시 텍스트는 흰색 */
}

.logout-button {
  padding: 10px 20px;
  background-color: #555; /* 중간 회색 버튼 */
  border: none;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s;
  border-radius: 3px;
  width: 100%;
  box-sizing: border-box;
}

.logout-button:hover {
  background-color: #444; /* 더 짙은 회색으로 변경 */
}
