/* BeginnerPage.css */
.music-generator-container {
  display: flex;
  justify-content: center; /* 수평 중앙 정렬 */
  align-items: center; /* 수직 중앙 정렬 */
  padding: 50px;
  min-height: 100vh; /* 화면 높이에 맞춰 중앙 정렬 유지 */
  background-color: #f0f2f5; /* 부드러운 배경색 추가 */
}

.music-generator-container h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

.music-generator-container form {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: fit-content; /* 내용물에 맞춰 폼의 너비가 조정되도록 설정 */
  padding: 25px;
  background-color: #ffffff;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.1); /* 카드 느낌의 그림자 */
  border-radius: 12px; /* 부드러운 모서리 */
}

.music-generator-container label {
  font-weight: bold;
  color: #333;
  font-size: 16px;
  text-align: center;
  margin-bottom: -5px; /* 제목과 선택 칸 간격 조절 */
}

.music-generator-container select {
  padding: 10px;
  font-size: 14px; /* 폰트 크기 약간 줄임 */
  border-radius: 8px;
  border: 1px solid #ddd;
  width: 150px; /* 선택 칸을 가로로 짧게 설정 */
  background-color: #f9f9fa;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.2s;
  text-align: center;
  align-self: center; /* 선택 칸을 중앙에 정렬 */
}

.music-generator-container select:hover {
  background-color: #e2e6ea;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.1); /* 호버 시 살짝 들어올려진 느낌 */
}

.submit-button {
  background-color: #333; /* 어두운 버튼 배경 */
  color: white;
  font-size: 16px;
  padding: 12px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
  width: 150px; /* 버튼이 선택 칸과 동일한 너비로 설정 */
  text-align: center;
  align-self: center; /* 버튼을 중앙에 정렬 */
  margin-top: 10px; /* 버튼과 위 요소 간의 간격 */
}

.submit-button:hover {
  background-color: #555; /* 호버 시 더 밝은 색상 */
  transform: scale(1.05); /* 살짝 확대되는 효과 */
}
