.admin-ai-management-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.sidebar {
  width: 250px;
  background-color: #2c3e50;
  color: #ecf0f1;
}

.content {
  flex: 1;
  padding: 40px;
  background-color: #ecf0f1;
}

.content-title {
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.ai-info {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.edit-button {
  padding: 5px 10px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 3px;
}
