/* Home.css */
.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 50px;
}

.service-title {
  font-size: 20px;
  color: #333;
  margin-bottom: 10px;
}

.main-title {
  font-size: 36px;
  font-weight: bold;
  margin: 0;
}

.description {
  font-size: 18px;
  color: #666;
  margin: 20px 0;
  line-height: 1.6;
}

.generate-button {
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
  background-color: #333;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.generate-button:hover {
  background-color: #555;
}

.image-container {
  margin-top: 30px;
}

.image-container img {
  max-width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}