.admin-review-detail-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.sidebar {
  background-color: #333;
  color: #fff;
  width: 250px;
  padding: 20px;
}

.main-content {
  flex: 1;
  padding: 40px;
  background-color: #fff;
}

.review-detail {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 5px;
}

.review-header {
  font-weight: bold;
  color: #000;
}

.back-button {
  background-color: #555;
  color: #fff;
  padding: 10px;
}

.back-button:hover {
  background-color: #444;
}
