/* SelectionPage.css */

.selection-page-container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 14px;
  background-color: #fff; /* 흰색 배경 */
  color: #000;
  text-align: center;
}

.content-wrapper {
  background: rgba(0, 0, 0, 0.05);
  padding: 28px;
  border-radius: 10px;
  max-width: 560px;
  width: 100%;
  box-shadow: 0 0 7px rgba(0, 0, 0, 0.1);
}

.main-title {
  font-size: 2.1rem;
  color: #000;
  margin-bottom: 7px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.subtitle {
  font-size: 0.84rem;
  color: #333;
  margin-bottom: 21px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.selection-title {
  font-size: 1.4rem;
  color: #000;
  margin-bottom: 21px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.buttons-container {
  display: flex;
  flex-direction: column;                 
  gap: 28px;
  margin-bottom: 35px;
}

.button-section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.selection-button {
  padding: 10.5px 21px;
  font-size: 0.84rem;
  color: #fff;
  background-color: #000;
  border: none;
  border-radius: 7px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
  width: 100%;
  max-width: 210px;
}

.selection-button:hover {
  background-color: #333;
  transform: scale(1.05);
}

.button-description {
  margin-top: 10.5px;
  font-size: 0.7rem;
  color: #555;
  line-height: 1.5;
  max-width: 350px;
}

.info-sections {
  display: flex;
  flex-direction: column;
  gap: 14px;
  margin-bottom: 21px;
}

.info-card {
  background: rgba(0, 0, 0, 0.05);
  padding: 14px;
  border-radius: 7px;
  text-align: left;
  color: #000;
  box-shadow: 0 0 3.5px rgba(0, 0, 0, 0.1);
}

.info-card h3 {
  font-size: 1.05rem; 
  color: #000;
  margin-bottom: 7px;
  border-bottom: 1px solid #000;
  padding-bottom: 3.5px;
  display: flex;
  align-items: center; /* 텍스트와 이미지 수직 정렬 */
  gap: 10px; /* 텍스트와 이미지 간 간격 */
}

.info-icon {
  width: 50px; /* 글자 크기에 맞게 설정 */
  height: 50px; /* 글자 크기에 맞게 설정 */
  object-fit: contain; /* 비율 유지하며 크기 조정 */
}

.info-card p {
  font-size: 0.7rem;
  color: #333;
  line-height: 1.5;
}

.footer-text {
  margin-top: 21px;
  font-size: 0.63rem;
  color: #777;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}
