body {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
}
.logo {
  width:15%; /* 부모 요소의 너비에 대한 백분율 */
  height: auto; /* 비율에 맞게 높이를 자동 조정 */
}

.admin-community-post-detail-container {
  display: flex;
  height: 100vh;
  width: 100%;
}

.sidebar {
  width: 250px;
  background-color: #111;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
}

.sidebar-logo {
  width: 80px;
  height: 80px;
  margin-bottom: 20px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  width: 100%;
}

.sidebar-menu li {
  width: 100%;
  padding: 15px;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.sidebar-menu li:hover,
.sidebar-menu .active {
  background-color: #333;
}

.logout-button {
  margin-top: auto;
  margin-bottom: 20px;
  padding: 10px;
  background-color: #444;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.logout-button:hover {
  background-color: #666;
}

.content {
  flex-grow: 1;
  padding: 40px;
  background-color: white;
}

.content-title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
}

.post-detail {
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  padding: 20px;
  border-radius: 10px;
}

.post-title {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 10px;
}

.post-content {
  margin-bottom: 20px;
  color: #555;
}

.comments-section {
  margin-top: 20px;
  padding-top: 10px;
  border-top: 1px solid #ddd;
}

.comment {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.comment-author {
  font-weight: bold;
  margin-right: 10px;
}

.comment-text {
  color: #555;
}

.delete-button {
  margin-top: 20px;
  padding: 10px;
  background-color: #d9534f;
  color: white;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.delete-button:hover {
  background-color: #c9302c;
}
