/* ExpertPage.css */
.music-generator-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px 20px;
  min-height: 100vh;
  background-color: #f8f9fc;
}

.music-generator-container h2 {
  font-size: 24px;
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

.music-generator-container form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 500px; /* 폼의 최대 너비를 제한 */
  padding: 30px;
  background-color: #ffffff;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}

.music-generator-container label {
  font-weight: bold;
  color: #333;
  font-size: 16px;
  text-align: center;
  margin-bottom: -10px; /* 제목과 선택 칸 간격 조절 */
}

.music-generator-container .chord-work {
  display: flex;
  gap: 8px; /* 선택 칸 사이 간격 */
  justify-content: center;
  flex-wrap: wrap; /* 화면에 맞춰 줄 바꿈 */
}

.music-generator-container .chord-work select {
  padding: 5px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ddd;
  width: 55px; /* 선택 칸의 가로 길이를 짧게 설정 */
  background-color: #ffffff;
  color: #333;
  text-align: center;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.2s;
}

.music-generator-container .chord-work select:hover {
  background-color: #f1f3f5;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.1); /* 살짝 들어올려진 느낌 */
}

.music-generator-container .main-instrument {
  display: flex;
  justify-content: center;
  width: 100%;
}

.music-generator-container .main-instrument select {
  padding: 10px;
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid #ddd;
  width: 100%;
  max-width: 200px;
  background-color: #ffffff;
  color: #333;
  cursor: pointer;
  transition: background-color 0.3s, box-shadow 0.2s;
}

.music-generator-container .main-instrument select:hover {
  background-color: #f1f3f5;
  box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.1);
}

.submit-button {
  background-color: #333;
  color: white;
  font-size: 16px;
  padding: 12px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.2s;
  width: 100%;
  max-width: 200px;
  text-align: center;
  margin: 0 auto;
  margin-top: 20px;
}

.submit-button:hover {
  background-color: #555;
  transform: scale(1.05);
}
