.customer-support-container {
  width: 60%; /* 가로 길이 설정 */
  margin: 20px auto; /* 중앙 정렬 */
  padding: 30px;
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 그림자 투명도 수정 */
}

h2 {
  text-align: center;
  color: #2c3e50;
  margin-bottom: 10px;
}

.tabs, .header-buttons {
  display: flex;
  justify-content: center;
  gap: 20px; /* 버튼 간격 */
  margin-bottom: 20px;
}

.tabs button, .header-buttons button {
  background-color: #ffffff;
  color: #2c3e50;
  padding: 10px 20px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease, border 0.3s ease;
}

.tabs button:hover, .header-buttons button:hover {
  background-color: #2980b9;
  color: #ffffff;
}

.tabs button.active, .header-buttons button.active {
  background-color: #2c3e50;
  color: #ffffff;
  border: 1px solid #2c3e50;
}

.pagination button {
  background-color: #ffffff;
  color: #2c3e50;
  padding: 8px 16px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease;
}

.pagination button:hover {
  background-color: #2980b9;
  color: #ffffff;
}

.pagination button.active {
  background-color: #2c3e50;
  color: #ffffff;
  border: 1px solid #2c3e50;
}

.pagination {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}
