/* CommunityPost.css */
.community-post-container {
  width: 80%; /* 동일한 폭 설정 */
  max-width: 800px; /* 최대 폭 800px */
  margin: 50px auto; /* 중앙 정렬과 위아래 여백 */
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1); /* 그림자 효과 */
}

h2 {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 20px;
  text-align: center; /* 제목 중앙 정렬 */
}

.form-group {
  margin-bottom: 15px; /* 동일한 필드 간격 */
}

label {
  font-weight: bold;
  margin-bottom: 5px;
  display: block;
}

input[type="text"], textarea {
  width: 100%; /* 입력 필드가 컨테이너 폭에 맞도록 설정 */
  padding: 12px;
  border-radius: 8px;
  border: 1px solid #ccc; /* 테두리 색상 */
  font-size: 16px; /* 폰트 크기 통일 */
  margin-bottom: 10px;
}

textarea {
  resize: none; /* 사용자가 크기 조정 못하도록 고정 */
  height: 100px; /* 기본 높이 설정 */
}

.submit-button {
  padding: 10px 20px; /* 버튼 내부 여백 설정 */
  font-size: 16px; /* 버튼 텍스트 크기 */
  background-color: #333;
  color: white;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease; /* 부드러운 색상 전환 */
}

.submit-button:hover {
  background-color: #555;
}
