/* src/styles/ReviewWrite.css */

.review-write-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f9f9f9; /* 밝은 회색 배경 */
  min-height: 100vh;
}

.review-write-container h2 {
  font-size: 1.5rem;
  color: #333;
  margin-bottom: 20px;
}

.review-write-container form {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 500px;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
}

.form-group label {
  font-size: 1rem;
  margin-bottom: 5px;
  color: #555;
}

.form-group input,
.form-group textarea {
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form-group input:focus,
.form-group textarea:focus {
  border-color: #666;
  outline: none;
}

.submit-button {
  padding: 10px 20px;
  font-size: 1rem;
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.submit-button:hover {
  background-color: #555;
}

@media (max-width: 600px) {
  .review-write-container {
    padding: 10px;
  }

  .review-write-container h2 {
    font-size: 1.2rem;
  }

  .review-write-container form {
    max-width: 100%;
  }

  .submit-button {
    width: 100%;
  }
}
