/* src/styles/ActivityHistory.css */

.activity-history-container {
  width: 60%;
  margin: 20px auto;
  padding: 20px;
  background-color: #f8f9fa; /* 전체 배경색 */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.activity-history-container h2 {
  text-align: center;
  color: #343a40; /* 제목 색상 */
  margin-bottom: 20px;
}

.activity-section {
  margin-bottom: 30px;
}

.activity-section h3 {
  color: #343a40;
  margin-bottom: 15px;
}

.error-message {
  color: red;
  margin-bottom: 15px;
  text-align: center;
}

.loading-message {
  text-align: center;
  font-size: 18px;
  color: #343a40;
}

.post-list {
  list-style: none;
  padding: 0;
}

.post-button {
  width: 100%;
  text-align: left;
  background-color: #ffffff; /* 게시글 박스 배경색 */
  border: 1px solid #ddd; /* 게시글 박스 테두리 */
  padding: 15px;
  margin-bottom: 15px;
  border-radius: 8px;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.post-button h4 {
  margin: 0 0 10px;
  font-size: 18px;
  color: #212529; /* 제목 텍스트 색상 */
}

.post-button p {
  margin: 5px 0;
  font-size: 14px;
  color: #495057; /* 내용 텍스트 색상 */
}

.post-button:hover {
  background-color: #f1f3f5; /* 호버 시 배경색 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* 호버 시 그림자 */
}

.write-button {
  padding: 10px 20px;
  background-color: #007bff; /* 버튼 배경색 */
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
  transition: background-color 0.3s ease;
}

.write-button:hover {
  background-color: #0056b3; /* 버튼 호버 색상 */
}

.pagination {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 3px;
  background-color: #6c757d;
  color: white;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.pagination button:hover {
  background-color: #5a6268;
}

.pagination button.active {
  background-color: #343a40;
}

.pagination button[disabled] {
  background-color: #adb5bd; /* 비활성화 버튼 색상 */
  cursor: default;
}
