/* src/styles/AdminCommunityBoard.css */

.admin-community-board-container {
  display: flex;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.admin-sidebar {
  width: 250px;
  background-color: #000; /* 짙은 블랙 배경 */
  color: #fff; /* 텍스트 흰색 */
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  box-sizing: border-box;
}

.sidebar-logo {
  width: 150px;
  height: auto;
  margin-bottom: 30px;
}

.sidebar-menu {
  list-style: none;
  padding: 0;
  width: 100%;
}

.sidebar-menu li {
  padding: 15px 20px;
  cursor: pointer;
  text-align: center;
  transition: background 0.3s ease;
  color: #ccc; /* 연한 회색 텍스트 */
}

.sidebar-menu li:hover,
.sidebar-menu li.active {
  background-color: #333;
  color: #fff; /* 활성 상태에서 흰색 텍스트 */
}

.logout-button {
  margin-top: auto;
  padding: 10px 20px;
  background-color: #555;
  border: none;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  width: 100%;
  box-sizing: border-box;
  transition: background 0.3s ease;
}

.logout-button:hover {
  background-color: #444; /* 더 짙은 회색 */
}

.main-content {
  flex: 1;
  padding: 40px;
  background-color: #ecf0f1; /* 밝은 회색 */
  overflow-y: auto;
  box-sizing: border-box;
}

.content-title {
  font-size: 2rem;
  margin-bottom: 20px;
  color: #2c3e50; /* 다크 블루 텍스트 */
}

/* 검색 폼 스타일링 */
.search-form {
  display: flex;
  align-items: center;
  margin-bottom: 30px; /* 공간을 더 주어 검색 영역을 강조 */
  max-width: 600px; /* 검색 폼의 최대 너비 설정 */
  margin-left: auto;
  margin-right: auto;
}

.search-select {
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-right: none;
  border-radius: 5px 0 0 5px;
  background-color: #fff;
  font-size: 1rem;
  color: #2c3e50;
  outline: none;
  transition: border 0.3s ease, box-shadow 0.3s ease;
  height: 40px; /* 높이 동일하게 설정 */
  box-sizing: border-box;
}

.search-select:focus {
  border: 1px solid #3498db;
  box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
}

.search-input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid #ccc;
  border-left: none;
  border-right: none;
  background-color: #fff;
  font-size: 1rem;
  color: #2c3e50;
  outline: none;
  transition: border 0.3s ease, box-shadow 0.3s ease;
  height: 40px; /* 높이 동일하게 설정 */
  box-sizing: border-box;
}

.search-input:focus {
  border: 1px solid #3498db;
  box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
}

.search-button {
  padding: 10px 20px;
  border: none;
  background-color: #3498db;
  color: #fff;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
  font-size: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px; /* 높이 동일하게 설정 */
  box-sizing: border-box;
}

.search-button i {
  margin-right: 8px;
}

.search-button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

.community-board-table {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.table-header,
.table-row {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  border-bottom: 1px solid #ddd;
  text-align: center;
}

.table-header {
  background-color: #3498db;
  color: #fff;
  font-weight: bold;
}

.table-row:hover {
  background-color: #f1f1f1;
}

.table-header span,
.table-row span {
  flex: 1;
}

.table-header span:last-child,
.table-row span:last-child {
  flex: 0.8;
}

.view-button,
.delete-button {
  padding: 8px 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 5px;
  transition: background 0.3s ease, transform 0.2s ease;
  font-size: 0.9rem;
}

.view-button {
  background-color: #2ecc71;
  color: #fff;
}

.view-button:hover {
  background-color: #27ae60;
  transform: translateY(-2px);
}

.delete-button {
  background-color: #e74c3c;
  color: #fff;
}

.delete-button:hover {
  background-color: #c0392b;
  transform: translateY(-2px);
}

.no-posts {
  padding: 20px;
  text-align: center;
  color: #7f8c8d;
}

.loading-message,
.error-message {
  text-align: center;
  color: #e74c3c;
  font-weight: bold;
  margin-top: 20px;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
}

.pagination button {
  margin: 0 5px;
  padding: 10px 16px;
  border: none;
  border-radius: 5px;
  background-color: #3498db;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
  font-size: 1rem;
}

.pagination button.active {
  background-color: #2c3e50;
}

.pagination button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.pagination span {
  font-size: 1rem;
  margin: 0 10px;
}

/* 반응형 디자인 */
@media (max-width: 600px) {
  .search-form {
    flex-direction: column;
    align-items: stretch;
  }

  .search-select,
  .search-input,
  .search-button {
    width: 100%;
    border-radius: 5px;
    margin-bottom: 10px;
    height: 40px; /* 높이 동일하게 유지 */
  }

  .search-button {
    border-radius: 5px;
    margin-left: 0;
  }
}
