.community-detail-container {
  width: 60%;
  margin: 20px auto;
  padding: 20px;
  background-color: #f8f9fa; /* 배경색 */
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h2 {
  text-align: center;
  color: #343a40;
  margin-bottom: 20px;
}

p {
  font-size: 16px;
  color: #495057;
  margin-bottom: 10px;
}

.button-group {
  display: flex;
  justify-content: space-between; /* 버튼 간격 조정 */
  margin-top: 20px;
}

.button-group button {
  flex: 1; /* 버튼을 동일한 너비로 */
  margin: 0 5px; /* 버튼 사이 간격 */
  padding: 10px 15px;
  font-size: 16px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: center;
}

.button-group button:hover {
  background-color: #0056b3; /* 호버 시 색상 */
}

.error-message {
  color: #e63946; /* 에러 메시지 색상 */
  background-color: #f8d7da;
  padding: 10px;
  border: 1px solid #f5c6cb;
  border-radius: 5px;
  margin-top: 10px;
}
